body {
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(182,11,141,1) 35%);
}
.App{
  background-color: #2a2a72;
  background: linear-gradient(300deg, #2a2a72 20%);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form input{
  background-color:#380036;
  border: 0;
  width: 200px;
  height: 50px;
  padding:  0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 8px;
  color: white;
}

#to-do-form input::placeholder{
  color: darkgray;
}

#to-do-form input:focus , #to-do-form button:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 90px;
  border-radius: 5px;
  border:0;
  background-color:rgb(252, 138, 9);
  font-size: 15px;
  cursor: pointer;
  font-weight: bolder;
  color:whitesmoke;
}

